.main-content {
    &.solution-teasier {
        .new-products {
            .title-block {
                margin-bottom: 16px;
            }
        }
        .solution-item_img{
            max-width: 600px;
            margin-bottom: 24px;
            @include media('large') {
                margin-bottom: 48px;
            }
        }
        .solutions-icon_wrapp {
            padding: 15px;
            border-radius: 50%;
            background-color: #f7f9ff;
            margin-right: 15px;
            align-self: flex-start;
            @include media('large') {
                padding: 23px;
                margin-right: 24px;
            }
            i {
                font-size: 25px;
                line-height: 25px;
                color: $bgbutton;
                @include media('large') {
                    font-size: 35px;
                    line-height: 35px;
                }
            }
        }
        .solution-item_content {
            h3 {
                margin-bottom: 8px;
            }
            p {
                color: #4D4D4D;
                margin-bottom: 18px;
                @include media('medium') {
                    margin-bottom: 24px;  
                }
                @include media('large') {
                    margin-bottom: 48px;  
                }
            }
            .desktop-app_version ,
            .mobile-app_version {
                display: inline-block;
                padding: 6px 12px;
                font-size: 14px;
                line-height: 14px;
                background-color: #F7F9FF;
                border-radius: 6px;
                margin-left: 8px;
            }
            .link-more {
                padding: 12px 24px;
                border: 1px solid $bgbutton;
                border-radius: 6px;
                margin-bottom: 18px;
                margin-top: auto;
                align-self: flex-start;
                @include media('medium') {
                    margin-bottom: 24px;
                }
                &:hover {
                    background-color: $bgbuttonhover;
                    border-color: $bgbuttonhover;
                    color: #fff;
                }
            }
        }
        .teasier-software {
            padding-bottom: 24px;
            @include media('medium') {
                padding-bottom: 48px;
            }
        }
        .solutions-desc_wrapp {
            height: 100%;
        }
    }
}