.first-about {
    &.partner-page {
        height: auto;
        @include media('extra') {
            height: 300px;
        }
        @include media('medium') {
            height: 450px;
        }
        @include media('large') {
            height: 782px;
        }
        .title-block {
            text-align: left;
            margin-bottom: 8px;
            padding-top: 10px;
            @include media('medium') {
                margin-bottom: 16px; 
                padding-top: 0;
            }
        }
        p {
            color: #fff;
            margin-bottom: 24px;
        }
    }
}
.partner-benefits {
    padding-top: 24px;
    @include media('medium') {
        padding-top: 48px;
    }
    @include media('medium') {
        padding-top: 72px;
    }
    .partner-ben_item {
        padding-top: 8px;
        margin-bottom: 12px;
        border-top: 2px solid $bgbutton;
        @include media('medium') {
            margin-bottom: 48px;
            padding-top: 18px;
            margin-bottom: 24px;
        }
        @include media('medium') {
            margin-bottom: 72px;
        }
        h3 {
            font-weight: bold;
            font-size: 24px;
            line-height: 28px;
            margin-bottom: 8px;
        }
        p {
            color: #4D4D4D;
            margin-bottom: 0px;
        }
    }
}
.about-partnership {
    padding-bottom: 24px;
    @include media('medium') {
        padding-bottom: 48px;
    }
    @include media('large') {
        padding-bottom: 72px;
    }
    h3 {
        font-size: 20px;
        line-height: 25px;
        font-weight: 500;
        color: #000000;
        margin-bottom: 12px;
        @include media('medium') {
            margin-bottom: 24px;
            font-size: 24px;
            line-height: 32px;
        }
    }
    p {
        color: #4D4D4D;
    }
    .partner-img_wrapp {
        margin-bottom: 10px;
        max-width: 600px;
        overflow: hidden;
        border-radius: 6px;
    }
}

.main-content {
    .footer-form {
        .resume-add_cv + label {
            background-color: #fff;
            color: #000;
            outline: none;
            border: none;
            i {
                color: $bgbutton;
            }
        }
    }

}
