.contacts {
    @include media('medium') {
        padding-bottom: 48px;
    }
    @include media('large') {
        padding-bottom: 72px;
    }
}
.contact-item {
    margin-bottom: 10px;
    @include media('medium') {
        margin-bottom: 0;
    }
    h5 {
        font-weight: bold;
        font-size: 16px;
        line-height: 26px;
        color: $bgbutton;
        margin-bottom: 5px;
    }
    p , a{
        font-weight: 500;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
        margin-bottom: 0;
        @include media('large') {
            font-size: 24px;
            line-height: 32px;
        }
    }
    a {
        transition: .3s;
        color: #000;
        &:hover {
            color: $bgbuttonhover;
        }
    }
    .social-list {
        justify-content: flex-start;
        li {
            &:not(:last-child) {
                margin-right: 7px;
                @include media('extra') {
                    margin-right: 20px;
                }
            }
        }
        a {
            font-size: 16px;
            line-height: 24px;
        }
    }
}

.support-teams {
    .contacts-info_tabs {
        display: flex;
        justify-content: center;

        a {
            font-weight: 500;
            font-size: 22px;
            line-height: 30px;
            color: #989898;
            padding-bottom: 12px;
            border-bottom: 2px solid transparent;
            @include media('medium') {
                padding-bottom: 24px;
                font-size: 24px;
                line-height: 32px;
            }
            &:not(:last-child) {
                margin-right: 72px;
            }
            &.current-contacts {
                color: #000000;
                border-color: $bgbutton;
            }
        }
    }
}

.contacts-products_tabs {
    padding-bottom: 24px;
    @include media('medium') {
        padding-bottom: 48px;
    }
    @include media('large') {
        padding-bottom: 72px;
    }
    .contact-item_tabs {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        height: 100%;
    }
    .contacts-tab_content {
        padding-top: 24px;
        display: none;
        border-top: 1px solid #E6E6E6;
        @include media('medium') {
            padding-top: 48px;
        }
        &.current-contacts {
            display: inherit;
        }
        .link-more {
            padding: 12px 24px;
            border: 1px solid $bgbutton;
            border-radius: 6px;
            margin-bottom: 24px;
            margin-top: auto;
            @include media('medium') {
                margin-bottom: 48px;
            }
            &:hover {
                border-color: $bgbuttonhover;
                background-color: $bgbuttonhover;
                color: #fff;
            }
        }
        .contact-img_wrapp {
            max-width: 288px;
            overflow: hidden;
            border-radius: 6px;
            margin-bottom: 24px;
            img {
                max-width: 170px;
            }
        }
        h3 {
            font-weight: bold;
            margin-bottom: 8px;
            font-size: 20px;
            line-height: 25px;
            @include media('medium') {
                font-size: 24px;
                line-height: 28px;
            }
        }
        h4 {
            font-size: 16px;
            line-height: 26px;
            color: #4D4D4D;
            margin-bottom: 12px;
            font-weight: normal;
            @include media('medium') {
                margin-bottom: 24px;
            }
        }
    }

}

.contact-triol-map {
    padding-bottom: 24px;
    @include media('medium') {
        padding-bottom: 48px;
    }
}

.sites-map {
    padding-bottom: 24px;
    @include media('medium') {
        padding-bottom: 48px;
    }
    @include media('large') {
        padding-bottom: 72px;
    }
    .sites-item_link {
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #E6E6E6;
        position: relative;
        padding-right: 30px;
        transition: .3s;
        margin-bottom: 16px;
        h4 {
            font-weight: bold;
            font-size: 16px;
            line-height: 26px;
            color: #000;
            margin-bottom: 5px;
        }
        span {
            font-weight: bold;
            font-size: 12px;
            line-height: 16px;            
            color: $bgbutton;
            margin-bottom: 5px;
            @include media('medium') {
                margin-bottom: 16px;
            }
        }
        i {
            position: absolute;
            right: 4px;
            top: 11px;
            font-size: 16px;
            color: #999999;
            transition: .3s;
        }
        &:hover {
            span {
                color: $bgbuttonhover;
            }
            i {
                right: 0;
            }
        }
    }
}